body {
  color: #0a0a0a;
  font-size: 16px;
  font-family: "Open Sans", sans-serif;
  line-height: 24px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

p {
  margin-top: 0;
  margin-bottom: 0px;
  font-size: 14px;
  line-height: 24px;
  color: #0a0a0a;
  font-weight: 400; }
  @media (new-breakpoint(min-width 768px)) {
    p {
      margin-bottom: 0px; } }
  @media (new-breakpoint(min-width 768px)) {
    p {
      font-size: 16px;
      line-height: 28px; } }

a {
  border-bottom: none;
  text-decoration: none;
  color: #0a0a0a;
  transition: color 0.3s ease; }
  a:active, a:focus, a:hover {
    color: black; }
  a [data-page-scroller] {
    cursor: pointer; }

blockquote {
  margin-top: 0;
  margin-bottom: 12px; }
  @media (new-breakpoint(min-width 768px)) {
    blockquote {
      margin-bottom: 24px; } }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 0px;
  color: #0a0a0a;
  -webkit-font-smoothing: antialiased; }
  @media (new-breakpoint(min-width 768px)) {
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
      margin-bottom: 0px; } }

h1 {
  font-size: 32px;
  line-height: 40px;
  font-weight: 300; }
  @media (new-breakpoint(min-width 768px)) {
    h1 {
      font-size: 40px;
      line-height: 48px; } }
  h1--headline {
    font-size: 32px;
    line-height: 40px; }
    @media (new-breakpoint(min-width 768px)) {
      h1--headline {
        font-size: 48px;
        line-height: 60px; } }

h2 {
  font-size: 28px;
  line-height: 36px;
  font-weight: 300; }
  @media (new-breakpoint(min-width 768px)) {
    h2 {
      font-size: 32px;
      line-height: 40px; } }

h3 {
  font-size: 24px;
  line-height: 36px;
  font-weight: 300; }
  @media (new-breakpoint(min-width 768px)) {
    h3 {
      font-size: 28px;
      line-height: 36px; } }

h4 {
  font-size: 18px;
  line-height: 32px;
  font-weight: 400; }
  @media (new-breakpoint(min-width 768px)) {
    h4 {
      font-size: 24px;
      line-height: 36px; } }

h5 {
  font-size: 16px;
  line-height: 28px;
  font-weight: 600; }
  @media (new-breakpoint(min-width 768px)) {
    h5 {
      font-size: 18px;
      line-height: 32px; } }

h6 {
  font-size: 13px;
  line-height: 24px;
  color: #333333;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 1.5px; }
  @media (new-breakpoint(min-width 768px)) {
    h6 {
      font-size: 13px;
      line-height: 24px; } }


input,
textarea,
select[multiple] {
  width: 100%;
  outline: none;
  color: #333333;
  vertical-align: top;
  height: 42px;
  border: 1px solid #cccccc;
  padding: 0 10px;
  box-sizing: border-box;
  font-size: 14px;
  box-shadow: 5px 5px 10px 0 rgba(51, 51, 51, 0.05);
  border-radius: 4px;
  transition: box-shadow 0.3s ease, border 0.3s ease; }
  
  input:hover,
  textarea:hover,
  select[multiple]:hover {
    box-shadow: 0 4px 8px 0 rgba(51, 51, 51, 0.12); }
  
  input:focus,
  textarea:focus,
  select[multiple]:focus {
    border-color: #013469;
    box-shadow: 5px 5px 10px 0 rgba(51, 51, 51, 0.05); }
  
  input:disabled,
  textarea:disabled,
  select[multiple]:disabled {
    opacity: 0.5;
    cursor: not-allowed;
    pointer-events: none; }
    
    input:disabled:hover,
    textarea:disabled:hover,
    select[multiple]:disabled:hover {
      box-shadow: 5px 5px 10px 0 rgba(51, 51, 51, 0.05); }

fieldset {
  margin-top: 0;
  margin-bottom: 12px;
  border: 0;
  padding: 0;
  background-color: transparent; }
  @media (new-breakpoint(min-width 768px)) {
    fieldset {
      margin-bottom: 24px; } }

legend {
  margin-top: 0;
  margin-bottom: 12px;
  padding: 0; }
  @media (new-breakpoint(min-width 768px)) {
    legend {
      margin-bottom: 24px; } }

label {
  display: block; }

textarea {
  padding: rem(11) rem(11); }

input[type='number'] {
  -moz-appearance: textfield; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none; }

[type='search'] {
  appearance: none; }

[type='radio'] {
  display: inline;
  margin-right: 12px; }

[type='file'] {
  margin-top: 0;
  margin-bottom: 12px;
  width: 100%; }
  @media (new-breakpoint(min-width 768px)) {
    [type='file'] {
      margin-bottom: 24px; } }

select {
  margin-top: 0;
  margin-bottom: 12px;
  max-width: 100%;
  width: auto; }
  @media (new-breakpoint(min-width 768px)) {
    select {
      margin-bottom: 24px; } }

ul,
ol {
  padding: 0;
  list-style-type: none;
  margin-top: 0;
  margin-bottom: 0px; }
  @media (new-breakpoint(min-width 768px)) {
    ul,
    ol {
      margin-bottom: 0px; } }
  ul:last-child,
  ol:last-child {
    margin-bottom: 0; }

dl {
  margin-top: 0;
  margin-bottom: 12px; }
  @media (new-breakpoint(min-width 768px)) {
