tr,
td,
th {
  vertical-align: middle; }

.toggle-button {
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -moz-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  -o-transform: translateY(-50%);
  transform: translateY(-50%);
  margin-right: 96px;
  float: left;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  height: 18px;
  width: 20px;
  color: red; }
  @media (min-width: 480px) {
    .toggle-button {
      display: none; } }
  .toggle-button:focus {
    outline: none; }
  .toggle-button__line {
    width: 100%;
    height: 2px;
    background: #ffffff;
    margin: 4px 0; }
    .toggle-button__line:first-of-type {
      margin-top: 0; }
    .toggle-button__line:last-of-type {
      margin-bottom: 0; }
